import type { IPoints } from '../interface';
import { utils } from './utils';

export function flip(points: IPoints, reg: RegExp, map: Record<string, string>): IPoints {
  const ret: string[] = [];
  utils.each(points, (p: string) => {
    ret.push(
      p.replace(reg, m => {
        return map[m];
      }),
    );
  });
  return ret as IPoints;
}
